<template>
    <div class="card" :style="{ backgroundImage: 'url(' + require('@/assets/img/'+img+'.'+extension) + ')' }" @click="goToUrl">

    </div>
</template>

<script>
    export default {
        name: "PortfolioCard",
        props: {
          img: String,
          url: String,
          extension: {
              default: 'png',
              type: String,
          },
        },
        methods: {
          goToUrl() {
              window.open(this.url, "_blank");
          }
        }
    }
</script>

<style lang="scss" scoped>
    .card{
        padding: 16px;
        height: 400px;
        width: 400px;
        border-radius: 4px;
        margin: 8px;
        color: #FFFFFF;
        cursor: pointer;
        box-shadow: 0px 0px 20px 0px rgba(50, 50, 50, 0.3);
        background-repeat: no-repeat;
        background-size: cover;
        transition: 0.2s;
        opacity: 0.9;
    }
    .card:hover{
        transform: scale(0.95);
        opacity: 1;
        box-shadow: 0px 0px 20px 0px rgba(50, 50, 50, 0.5);
    }
</style>
